var getRmb=require('../../common/requset.js')
export default {
    data(){
        return {
            labnrv:'',
            type:'卖单',
            datn:'',
            state:'',
            stw:'',
            price:'',
            rmb:'',
            frte:true,
            checkall:'展示完整信息'
        }
    },
    onLoad(option) {
        
        var that = this
        //获取上个页面携带的数据，并复制
        var datn = JSON.parse(option.aser)
        that.datn = datn
        var stw = that.datn[1]
        that.stw = that.datn[1]
        
        that.labnrv = that.datn[0][0]
        that.state = that.datn[0][0].order_status
        that.price = that.datn[0][0].sale_money
        if(that.state == 101) {
            that.state = '待付款'
        }
        that.rmb = getRmb.getrmb(that.price)
    },
    methods:{
        btn:function () {
            var that = this
            that.frte=!that.frte;
            if(that.frte==false){
                that.checkall='收起';
            }else{
                that.checkall='展示完整信息'
            }
        }
    }
}
